import { Table, Button, Popconfirm, message } from "antd";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
} from "react";
import dayjs from "dayjs";
import { area_delete, area_get } from "../../../api/area";
// import mock from ''
const ListTable = (props, ref) => {
  useImperativeHandle(ref, () => ({
    // changeVal 就是暴露给父组件的方法
    getData: (newVal) => {
      area_get_handle(newVal);
    },
  }));
  const { update_h, pageSize = 10, total = 0, showTotal = true } = props;
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: pageSize || 10,
    total: total || 0,
    showSizeChanger: true,
  });
  //删除地区
  const delete_h = async (id) => {
    try {
      await area_delete(id);
      message.success("删除成功");
      area_get_handle();
    } catch (error) {
      console.error(error);
    }
  };
  const columns = [
    { title: "ID", dataIndex: "id", key: "id" },
    { title: "appid", dataIndex: "appid" },
    { title: "region", dataIndex: "region" },
    {
      title: "操作",
      dataIndex: "operation",
      align: "center",
      width: 200,
      render: (text, record, index) => {
        return (
          <span>
            <Button type="link" onClick={() => update_h(record)}>
              编辑
            </Button>
            <Popconfirm
              title="确定删除这个地区?"
              onConfirm={() => delete_h(record.id)}
              okText="确定"
              cancelText="取消"
            >
              <a href="#">删除</a>
            </Popconfirm>
          </span>
        );
      },
    },
  ];
  //获取列表
  const area_get_handle = async (params = {}) => {
    try {
      setLoading(true);
      let data = await area_get({
        page: pagination.current,
        page_size: pagination.pageSize,
        ...params,
      });
      setData(data.data);
        setPagination({
        ...pagination,
        total: data.page.count,
      });
      // console.log(pagination);
      setLoading(false);
    } catch (e) {
      setLoading(false);
      console.error(e);
    }
  };
  //表格改变触发
  const onchange = (pagination) => {
    // console.log(pagination)
    setPagination({ ...pagination });
  };
  useEffect(() => {
    area_get_handle();
  }, [pagination.current, pagination.pageSize]);
  return (
    <>
      <Table
        bordered
        loading={loading}
        columns={columns}
        dataSource={data}
        rowKey="id"
        pagination={pagination}
        onChange={onchange}
      ></Table>
    </>
  );
};
export default forwardRef(ListTable);
