import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  InputNumber,
} from "antd";
import { Tree } from "antd";
import { area_update } from "../../../api/area";
const { Option } = Select;
const { TextArea } = Input;
const Index = (props) => {
  const { onCancel, visible, onOk, info } = props;
  const [form] = Form.useForm();
  //提交
  const submit = async () => {
    // console.log(form.getFieldsValue());
    try {
      form.validateFields().then(async () => {
        await area_update({
          ...form.getFieldsValue(),
          id: info.id,
        });
        message.success("修改成功");
        onOk();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };

  const on_cancel = () => {
    onCancel();
  };
  useEffect(() => {
    if (visible) {
      form.setFieldsValue({
        ...info,
      });
    }
  }, [visible]);
  const reset_h = () => {
    // console.log(2324)
    form.resetFields();
  };
  return (
    <Modal
      title="编辑地区"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
      afterClose={reset_h}
      cancelText="返回"
      okText="确定"
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        {/* <Form.Item
          label="id"
          name="id"
          rules={[{ required: true, message: "请输入id" }]}
        >
          <Input disabled placeholder="请输入id" />
        </Form.Item> */}
        <Form.Item
          label="APPID"
          name="appid"
          rules={[{ required: true, message: "请输入APPID" }]}
        >
          <InputNumber placeholder="请输入APPID" />
        </Form.Item>

        <Form.Item
          label="Region"
          name="region"
          rules={[{ required: true, message: "请输入Region" }]}
        >
          <Input placeholder="请输入Region" />
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(Index);
