import axios from "utils/http";

//获取区域列表
export const area_get = (params) => {
  return axios.get(`/admin/regions`, params);
};
//新增区域
export const area_add = (params) => {
  return axios.jpost(`/admin/regions`, params);
};

//修改区域
export const area_update = (params) => {
  return axios.put(`/admin/regions`, params);
};

//删除区域
export const area_delete = (params) => {
  return axios.delete(`/admin/regions/${params}`);
};
